:root {
  --bg: #0b0d12;
  --bg-2: #07080c;
  --surface: rgba(255, 255, 255, 0.05);
  --surface-2: rgba(255, 255, 255, 0.08);
  --border: rgba(255, 255, 255, 0.10);
  --text: rgba(255, 255, 255, 0.92);
  --muted: rgba(255, 255, 255, 0.62);
  --accent: #7c5cff;
  --accent-2: #4cd3ff;
  --free: #2cff9a;
  --warning: #ffcc66;
  --listen-glow: rgba(124,92,255,0.55);
  --listen-glow-soft: rgba(124,92,255,0.18);
  --listen-core: #7c5cff;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: transparent;
  color: var(--text);
  font-family: Arial, Helvetica, sans-serif;
}
.navItem {
  padding: 10px 12px;
  border-radius: 12px;
  color: rgba(255,255,255,0.75);
  transition: all 0.18s ease;
  border: 1px solid transparent;
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}
.navItem:hover {
  background: rgba(255,255,255,0.06);
  color: rgba(255,255,255,0.98);
  border-color: rgba(255,255,255,0.12);
  transform: translateX(6px);
}
.navItemActive {
  padding: 10px 12px;
  border-radius: 12px;
  background: rgba(124,92,255,0.18);
  border: 1px solid rgba(124,92,255,0.35);
  color: white;
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}
.navItemActive::before {
  content: "";
  position: absolute;
  left: -10px;
  top: 6px;
  height: calc(100% - 12px);
  width: 4px;
  border-radius: 4px;
  background: linear-gradient(180deg, #9b8cff, #6c4dff);
  box-shadow: 0 0 12px rgba(124,92,255,0.9);
}
.listenButton {
  background: radial-gradient(circle at 30% 30%, #b7a8ff, #7c5cff);
  color: white;
  font-weight: 600;
  letter-spacing: 0.18em;
  font-size: 14px;
  box-shadow: 0 0 40px rgba(124,92,255,0.7),
    0 0 120px rgba(124,92,255,0.4),
    inset 0 0 30px rgba(255,255,255,0.25);
  transition: all 0.25s ease;
}
.listenButton:hover {
  transform: scale(1.08);
}
.listenGlow {
  background: radial-gradient(circle, rgba(124,92,255,0.45), transparent 70%);
  filter: blur(60px);
  opacity: 0.9;
  pointer-events: none;
}
.listenRing {
  border: 2px solid rgba(124,92,255,0.45);
  animation: pulseRing 2.6s infinite ease-out;
  pointer-events: none;
}
@keyframes pulseRing {
  0% {
    transform: scale(0.85);
    opacity: 0.9;
  }
  70% {
    transform: scale(1.25);
    opacity: 0.15;
  }
  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}
.card {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.10);
  border-radius: 18px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.35);
}
.cardTitle {
  color: rgba(255,255,255,0.92);
}
.cardText {
  color: rgba(255,255,255,0.62);
}
.badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid rgba(124,92,255,0.28);
  background: rgba(124,92,255,0.16);
  color: rgba(255,255,255,0.92);
  font-size: 12px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}
.kpi {
  background: rgba(255,255,255,0.04);
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 16px;
  padding: 14px 16px;
}
.listenAura {
  background: radial-gradient(circle, rgba(124,92,255,0.18), transparent 70%);
  filter: blur(80px);
  opacity: 0.9;
  pointer-events: none;
}
.listenWave {
  border: 2px solid rgba(124,92,255,0.35);
  animation: waveExpand 2.6s infinite ease-out;
  pointer-events: none;
}
@keyframes waveExpand {
  0% {
    transform: scale(0.7);
    opacity: 0.8;
  }
  70% {
    transform: scale(1.3);
    opacity: 0.2;
  }
  100% {
    transform: scale(1.6);
    opacity: 0;
  }
}
.listenRing {
  border: 2px solid rgba(124,92,255,0.45);
  animation: innerPulse 2s infinite ease-in-out;
  pointer-events: none;
}
@keyframes innerPulse {
  0% {
    transform: scale(0.9);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.3;
  }
  100% {
    transform: scale(0.9);
    opacity: 0.7;
  }
}
.listeningActive {
  animation: buttonBreath 1.3s infinite ease-in-out;
}
@keyframes buttonBreath {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.12);
  }
  100% {
    transform: scale(1);
  }
}
.logoWrapper {
  cursor: pointer;
}
.logoText {
  font-size: large;
  color: #9b8cff;
  font-weight: 600;
  letter-spacing: 0.02em;
  text-shadow: 0 0 10px rgba(124,92,255,0.75),
    0 0 25px rgba(124,92,255,0.45),
    0 0 45px rgba(124,92,255,0.25);
  transition: all 0.25s ease;
}
.logoWrapper:hover .logoText {
  color: #a99bff;
  letter-spacing: 0.05em;
  text-shadow: 0 0 18px rgba(124,92,255,1),
    0 0 40px rgba(124,92,255,0.7),
    0 0 70px rgba(124,92,255,0.45);
}
.logoDot {
  width: 8px;
  height: 8px;
  border-radius: 999px;
  background: #7c5cff;
  box-shadow: 0 0 8px #7c5cff,
    0 0 18px rgba(124,92,255,0.8),
    0 0 28px rgba(124,92,255,0.6);
  animation: pulseLogo 2s infinite ease-in-out;
}
@keyframes pulseLogo {
  0% {
    transform: scale(0.9);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.3);
    opacity: 1;
  }
  100% {
    transform: scale(0.9);
    opacity: 0.7;
  }
}
.resultEnter {
  animation: resultIn 520ms cubic-bezier(.2,.9,.2,1) both;
}
@keyframes resultIn {
  0% {
    opacity: 0;
    transform: translateY(14px) scale(0.985);
    filter: blur(6px);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }
}
.recentHighlight {
  animation: recentGlow 1.6s ease-out both;
  border-radius: 18px;
}
@keyframes recentGlow {
  0% {
    box-shadow: 0 0 0 rgba(124,92,255,0);
    transform: translateY(6px);
  }
  40% {
    box-shadow: 0 0 28px rgba(124,92,255,0.22);
    transform: translateY(0);
  }
  100% {
    box-shadow: 0 0 0 rgba(124,92,255,0);
  }
}
@media (prefers-reduced-motion: reduce) {
  .resultEnter,
  .recentHighlight {
    animation: none !important;
  }
}
